  overflow: hidden;
  -webkit-transition: height .4s;
  transition: height .4s
}

.cd-horizontal-timeline .events-content>ol>li {
  position: absolute;
  z-index: 1;
  width: 100%;
  left: 0;
  top: 0;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
