.mt-element-ribbon {
  position: relative;
  margin-bottom: 30px
}

.mt-element-ribbon .ribbon-content {
  margin: 0;
  padding: 25px;
  clear: both
}

.mt-element-ribbon .ribbon-content.no-padding {
  padding-top: 0
}

.mt-element-ribbon .ribbon {
  padding: .5em 1em;
  z-index: 5;
  float: left;
  margin: 10px 0 0 -2px;
  clear: left;
  position: relative
}

.mt-element-ribbon .ribbon.ribbon-right {
  float: right;
  clear: right;
  margin: 10px -2px 0 0
}

.mt-element-ribbon .ribbon.ribbon-vertical-left {
  clear: none;
  margin: -2px 0 0 10px;
  padding-top: 1em;
  padding-bottom: 1em;
  width: 41px;
  text-align: center
}

.mt-element-ribbon .ribbon.ribbon-vertical-right {
  clear: none;
  float: right;
  margin: -2px 10px 0 0;
  padding-top: 1em;
  padding-bottom: 1em;
  width: 41px;
  text-align: center
}

.mt-element-ribbon .ribbon.ribbon-shadow {
  -webkit-box-shadow: 2px 2px 7px rgba(0, 0, 0, .4);
          box-shadow: 2px 2px 7px rgba(0, 0, 0, .4)
}

.mt-element-ribbon .ribbon.ribbon-shadow.ribbon-right,
.mt-element-ribbon .ribbon.ribbon-shadow.ribbon-vertical-right {
  -webkit-box-shadow: -2px 2px 7px rgba(0, 0, 0, .4);
          box-shadow: -2px 2px 7px rgba(0, 0, 0, .4)
}

.mt-element-ribbon .ribbon.ribbon-round {
  border-top-right-radius: 5px !important;
  border-bottom-right-radius: 5px !important
}

.mt-element-ribbon .ribbon.ribbon-round.ribbon-right {
  border-radius: 5px 0 0 5px !important
}

.mt-element-ribbon .ribbon.ribbon-round.ribbon-vertical-left,
.mt-element-ribbon .ribbon.ribbon-round.ribbon-vertical-right {
  border-radius: 0 0 5px 5px !important
}

.mt-element-ribbon .ribbon.ribbon-border:after {
  border: 1px solid;
  content: '';
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px
}

.mt-element-ribbon .ribbon.ribbon-border-vert:after {
  border-top: none;
  border-bottom: none;
  border-left: 1px solid;
  border-right: 1px solid;
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 5px;
  right: 5px
}

.mt-element-ribbon .ribbon.ribbon-border-hor:after {
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-left: none;
  border-right: none;
  content: '';
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 0;
  right: 0
}

.mt-element-ribbon .ribbon.ribbon-border-dash:after {
  border: 1px dashed;
  content: '';
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px
}

.mt-element-ribbon .ribbon.ribbon-border-dash-vert:after {
  border-top: none;
  border-bottom: none;
  border-left: 1px solid;
  border-right: 1px solid;
  border-left-style: dashed;
  border-right-style: dashed;
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 5px;
  right: 5px
}

.mt-element-ribbon .ribbon.ribbon-border-dash-hor:after {
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-left: none;
  border-right: none;
  border-top-style: dashed;
  border-bottom-style: dashed;
  content: '';
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 0;
  right: 0
}

.mt-element-ribbon .ribbon.ribbon-clip {
  left: -10px;
  margin-left: 0
}

.mt-element-ribbon .ribbon.ribbon-clip.ribbon-right {
  left: auto;
  right: -10px;
  margin-right: 0
}

.mt-element-ribbon .ribbon>.ribbon-sub {
  z-index: -1;
  position: absolute;
  padding: 0;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0
}

.mt-element-ribbon .ribbon>.ribbon-sub.ribbon-clip:after,
